.MainContainer {
  background: linear-gradient(48deg, rgb(34, 36, 37) 0%, rgb(33, 34, 34) 46%, rgb(36, 37, 37) 52%, rgb(36, 36, 37) 100%);
  background-size: 400% 400%;
  animation: gradientAnimation 14s ease infinite;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-family: "Sequel100Black";
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.ContentContainer {
  height: calc(100% - 60px);
  width: calc(100% - 60px);
}

.TopRow {
  height: 16%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Title {
  font-weight: bold;
  font-size: 80px;
  letter-spacing: 16.9px;
  padding-bottom: 50px;
  width: 71%;
}

.TitleInfo {
  font-size: 15px;
  letter-spacing: 5px;
  width: 29%;
  line-height: 30px;
  padding-left: 25px;
  border-left: 3px solid #fff;
}

@media (min-width: 400px) {
  .Title {
    font-size: 40px;
  }

  .TitleInfo {
    letter-spacing: 0px;
    font-size: 10px;
  }
}

@media (min-width: 800px) {

  .TitleInfo {
    letter-spacing: 5px;
    font-size: 15px;
  }
}

@media (min-width: 1600px) {
  .Title {
    font-size: 80px;
  }
}

.MiddleRow {
  height: 54%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 80px;
}

.Button:hover {
  background-color: #fff;
  color: #000;
}

.ShowcaseVideo {
  width: 100%;
  height: 100%;
}

.ShowcaseButtons {
  height: 40px;
  width: 100%;
}

.BottomRow {
  height: 30%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.MediaContainer {
  width: 400px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: end;
  font-size: 15px;
}

@media (min-width: 400px) {
  .MediaContainer {
    font-size: 11px;
  }
}

@media (min-width: 800px) {
  .MediaContainer {
    font-size: 15px;
  }
}

.DiscordButton {
  width: 200px;
  height: 45px;
  font-size: 16px;
  margin-bottom: 20px;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  background-color: #272727;
  border: 2px solid #7289da;
  color: #7289da;
  transition: all 0.3s ease;
  font-family: "Sequel100Black";
  border-radius: 15px;
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.1), 6px 6px 12px rgba(0, 0, 0, 0.7);
}


@media (min-width: 400px) {
  .DiscordButton {
    height: 35px;
    font-size: 15px;
    width: 110px;
  }
}

@media (min-width: 800px) {
  .DiscordButton {
    height: 45px;
    font-size: 16px;
    width: 200px;
  }
}

.DiscordButton:hover {
  scale: 1.05;
  background-color: #7289da;
  border: 2px solid #7289da;
  color: #fff;
}

.ContactContainer {
  width: 500px;
  height: 200px;
  border-radius: 15px;
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.1), 6px 6px 12px rgba(0, 0, 0, 0.7);
  background-color: #272727;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.ContactButton {
  width: 60%;
  height: 50px;
  font-size: 16px;
  margin-top: 20px;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  background-color: #272727;
  border: 2px solid #fff;
  color: #fff;
  transition: all 0.3s ease;
  font-family: "Sequel100Black";
  border-radius: 15px;
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.1), 6px 6px 12px rgba(0, 0, 0, 0.7);
}

.ContactButton:hover {
  scale: 1.05;
  background-color: #fff;
  border: 2px solid #272727;
  color: #272727;
}

@media (min-width: 250px) {

  .ContactContainer {
    width: 300px;
    height: 100px;
    font-size: 12px;
  }

  .ContactButton {
    font-size: 13px;
    margin-top: 0px;
  }
}

@media (min-width: 1000px) {
  .ContactContainer {
    width: 500px;
    height: 200px;
    font-size: 16px;
  }

  .ContactButton {
    font-size: 14px;
    margin-top: 0px;
  }
}


.InformationContainer {
  width: 400px;
  height: 200px;
  border-radius: 15px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: end;
  text-align: end;
  margin-top: 20px;
}



.KiddoLogoImg {
  width: 80px;
  height: auto;
  border: 2px solid #000;
  border-radius: 15px;
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.1), 6px 6px 12px rgba(0, 0, 0, 0.7);
  transition: 0.15s;
  cursor: pointer;
}

@media (min-width: 250px) {

  .KiddoLogoImg {
    width: 60px;
  }
}

@media (min-width: 600px) {
  .KiddoLogoImg {
    width: 80px;
  }
}

@media (min-width: 250px) {

  .InformationContainer {
    font-size: 8px;
  }
}

@media (min-width: 600px) {
  .InformationContainer {
    font-size: 14px;
  }
}

.KiddoLogo:hover {
  scale: 1.02;
}